let tasks = JSON.parse(localStorage.getItem('tasks')) || [];

const saveTasksToLocalStorage = () => {
  localStorage.setItem('tasks', JSON.stringify(tasks));
}

const populateTasks = () => {
    const listElement = document.createElement('ul');
    listElement.className = 'todo-list';
  
    tasks.forEach((task) => {
      const listItem = document.createElement('li');
      listItem.className = 'list-item';
      listItem.innerHTML = `
        <input type="checkbox" ${task.completed ? 'checked' : ''}>
        ${task.description}
      `;
      listElement.appendChild(listItem);
    });
  
    document.getElementById('container').appendChild(listElement);
  }

const addTask = (description) => {
  const newTask = {
    description,
    completed: false,
    index: tasks.length + 1,
  };
  tasks.push(newTask);
  saveTasksToLocalStorage();
  populateTasks();
}

const removeTask = (index) => {
  tasks.splice(index - 1, 1);
  tasks.forEach((task, idx) => (task.index = idx + 1));
  saveTasksToLocalStorage();
  populateTasks();
}

const editTask = (index, newDescription) => {
  tasks[index - 1].description = newDescription;
  saveTasksToLocalStorage();
}

const clearCompletedTasks = () => {
  tasks = tasks.filter(task => !task.completed);
  tasks.forEach((task, idx) => (task.index = idx + 1));
  saveTasksToLocalStorage();
  populateTasks();
}

export { populateTasks, addTask, removeTask, editTask, clearCompletedTasks };
